import './App.css';
import { useCallback, useState } from 'react';
import io from 'socket.io-client';

function App() {
  const [nickname, setNickname] = useState('');
  const [messages, setMessages] = useState([]);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [socket, setSocket] = useState(null);

  const authenticate = useCallback(() => {
    setIsAuthenticated(true);

    const socket = new io(`http://localhost:3000`);
    setSocket(socket);

    socket.on('text-chat', (msg) => {
      setMessages(oldMessages => [...oldMessages, msg]);
    });
    socket.on('exception', () => {
      window.alert('Invalid message sent');
    });

  }, []);

  const sendMessage = (nickname, message) => {
    socket?.emit('text-chat', {nickname, message});
  }

  return (
    <div className="app">
      {!isAuthenticated && <AuthWall nickname={nickname} setNickname={setNickname} authenticate={authenticate} />}
      {isAuthenticated && (
        <Chat messages={messages} nickname={nickname} sendMessage={sendMessage}/>
      )}
    </div>
  );
}

function AuthWall({nickname, setNickname, authenticate}) {
  const onEnterPress = (k) => {
    if(k.key === 'Enter' && nickname) {
      authenticate()
    }
  }
  return (
    <div className="input-wrapper">
          <h3 style={{color: 'black'}}>Enter your name: </h3>
          <input value={nickname} onChange={(e) => setNickname(e.target.value)} placeholder="Enter your name" onKeyDown={onEnterPress}/>
          <button disabled={!nickname} onClick={authenticate} >Enter chat</button>
        </div>
  )
}

function ChatMessage({isMyOwn, message, time, name}) {
  return (
    <div className="message" style={{
      margin: isMyOwn ? '8px auto 8px 8px' : '8px 8px 8px auto',
      backgroundColor: isMyOwn ? '#867AFF' : '#675AED',
      }}>
      <div className='message-header'>
        <p className='name'>{isMyOwn ? 'Me' : name}</p>
        <p>{time}</p>
      </div>
      <div>
        <p>{message}</p>
      </div>
    </div>
  )
}

function Chat({messages, nickname, sendMessage}) {
  const [formValue, setFormValue] = useState('');

  const onFormSubmit = (e) => {
    e.preventDefault();
    sendMessage(nickname, formValue);
    setFormValue('');
  }

  return(
  <div className='chat'>
    <div className='message-box'>
      {messages.length > 0 && messages.map((msg, i) => (<ChatMessage key={i} isMyOwn={msg.nickname === nickname} message={msg.message} time={msg.time} name={nickname}/>))}
      {messages.length === 0 && <div className='empty-messages-box '>There are no messages yet</div>}
    </div>
    <div className='new-message-form-wrapper'>
    <form onSubmit={onFormSubmit} className="input-wrapper ">
      <input value={formValue} onChange={(e) => setFormValue(e.target.value)} placeholder="say something nice" />
      <button type="submit" disabled={!formValue}>Send</button>
    </form>
    </div>
  </div>
  );
}

export default App;
